body {
  background: radial-gradient(
    circle, rgba(238,174,202,1) 1%, rgba(148,187,233,0.37) 100%
  );
}

.hidden-button {
  display: none;
}

.product__title {
  font-size: 36px;
  text-align: center;
}

.product__list {
  padding: 0;
}

.product__item {
  line-height: 150%;
  font-size: 24px;
  list-style: none;
  text-align: center;
}

.product__container-button {
  margin: 0 auto;
  width: max-content;
}

.product__button {
  margin: 0 5px;
  padding: 5px 10px;

  border-radius: 10px;
  border: 1px solid;

  background-color: transparent;
  cursor: pointer;
  font-size: 18px;
}
